.card {
    width: 100%;
    height: 100%;
    color: rgb(75, 145, 18);
    font-size: 15px;
    font-family: sans-serif;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transform-style: preserve-3d;
    border-radius: 15px;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    &_front {
        backface-visibility: hidden; }
    &_back {
        backface-visibility: hidden;
        background: url('../../images/card_back4.jpg') no-repeat center;
        background-size: cover; }
    &-container {
        background: red; } }

